<template>
    <div>
        <el-table :data="moduleData.list" style="width: 100%">
            <el-table-column prop="agenda_id" label="#" width="70"></el-table-column>
            <el-table-column prop="title" label="" width="100"></el-table-column>
            <el-table-column prop="start_time" label="" width="180"></el-table-column>
            <el-table-column prop="end_time" label="" width="180"></el-table-column>
            <el-table-column prop="hostess_id" label=""></el-table-column>
        </el-table>
    </div>
</template>
<script>
//  => 
export default {
    props: ['meetingId'],
    data () {
        return {
            moduleData: {
                list: []
            }
        }
    },
    created() {
        this.getModuleData()
    },
    methods: {
        getModuleData() {
            this.$api.apiCommunication('/Meeting/getAgendaList', { conference_id: this.meetingId }, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list ? response.data.list : []
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        }
    }
}
</script>



