<template>
    <div>
        <h1 class="wrap-title mar-bottom15">
            <span> > </span>
            <el-button type="primary" @click="addEditorData(0, 'add')" size="mini" class="bread-addbtn"></el-button>
            <feng-excel-upload text="" url="/Upload/excelUpload" @increment="responesData" :params="excelParams"></feng-excel-upload>
        </h1>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item pdd-10">
                <el-table :data="moduleData.list" style="width: 100%">
                    <el-table-column prop="seat_id" label="#" width="70"></el-table-column>
                    <el-table-column prop="seat_code" label="" width="150" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="seat_ip" label="ip" width="140"></el-table-column>
                    <el-table-column prop="coordinate" label="" width="140"></el-table-column>
                    <el-table-column label="" min-width="180">
                        <template slot-scope="scope">
                            <el-button type="primary" @click="addEditorData(scope, 'editor')" size="small"></el-button>
						</template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item pdd-10">
                <p class="mar-bottom10"></p>
                <img :src="imgMap" class="meeting-imgmap">
            </el-col>
        </el-row>
        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible">
            <el-form ref="form" :model="form" label-width="95px" class="content-form">
                <el-form-item label="">
                    <el-input v-model="form.seat_code"></el-input>
                </el-form-item>
                <el-form-item label="ip">
                    <el-input v-model="form.seat_ip"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.coordinate"></el-input>
                </el-form-item>
                <el-form-item label=" ">
                    <el-button type="primary" size="small" @click="submitEditor('form')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
//  => 
import '@/static/style/meeting/index.scss'
import fengExcelUpload from '@/components/fengModule/excelUpload.vue'


export default {
    props: ['id'],
    data () {
        return {
            marked: 'add',
            dialogVisible: false,
            form: {
                seat_code: '',
                seat_ip: '',
                coordinate: ''
            },
            imgMap: '',
            moduleData: {
                list: []
            },
            excelParams: {
                room_id: this.id
            }
        }
    },
    created() {
        this.getModuleData()
        this.getRoomsInfo()
    },
    computed: {
        dialogTitle() { return this.marked === 'add' ? '' : '' }
    },
    methods: {
        getModuleData() {
            // 
            this.$api.apiCommunication('/Rooms/getRoomsSeatsList', { room_id: this.id }, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        getRoomsInfo() {
            // 
            this.$api.apiCommunication('/Rooms/getRoomsInfo', { room_id: this.id }, response => {
                if (response.status === 200) {
                    this.imgMap = response.data.img_map
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        addEditorData(val, type) {
            if(type === 'editor') {
                let param = val.row
                delete param.create_time
                delete param.room_id
                delete param.status
                delete param.update_time
                this.form = param
            } else {
                this.form = {
                    seat_code: '',
                    seat_ip: '',
                    coordinate: ''
                }
            }
            this.marked = type
            this.dialogVisible = true
        },
        responesData(response) {
            // excel
            if(response.status === 200){
                // 
                this.$notify({ title: '', message: '', type: 'success' })
                this.getModuleData()
                return false
            }
            this.$alert(`${response.msg}`, '', { confirmButtonText: '', type: 'error' })
        },
        submitEditor(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = {
                            room_id: this.id
                        },
                        apiname = this.marked === 'add' ? '/Rooms/createRoomsSeats' : '/Rooms/updateRoomsSeats'
                    Object.assign(param, this.form)
                    // if(this.marked === 'add') {
                    //     apiname = '/Rooms/createRoomsSeats'
                    // } else {
                    //     apiname = '/Rooms/updateRoomsSeats'
                    //     // Object.assign(param, { seat_id:  })
                    // }
                    this.$api.apiCommunication(apiname, param, response => {
                        this.dialogVisible = false
                        if (response.status === 200) {
                            this.getModuleData()
                        } else {
                            this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                        }
                    })
                } else {
                    this.$notify({ title: '', message: '', type: 'error' })
                }
            })
        }
    },
    components: {
        fengExcelUpload
    }
}
</script>



