<template>
    <el-form ref="form" :model="form" label-width="80px" class="content-form" style="width: 100%;">
        <el-form-item label=":">
            <el-input v-model="form.emailUser" placeholder="" style="width: 100%;"></el-input>
        </el-form-item>
        <el-form-item label=":">
            <el-input v-model="form.title" placeholder="" style="width: 100%;"></el-input>
        </el-form-item>
        <el-form-item label=":">
            <el-input v-model="form.content" type="textarea" placeholder="" :rows="8"></el-input>
        </el-form-item>
        <el-form-item label="">
            <feng-file-upload text="" url="/Upload/filesUpload" @increment="handleAvatarScucess"></feng-file-upload>
        </el-form-item>
        <el-form-item label=" ">
            <el-button type="primary" size="small" @click="submit('form')"></el-button>
        </el-form-item>
    </el-form>
</template>
<script>
//  => 
import fengFileUpload from '@/components/fengModule/fileUpload.vue'

export default {
    data() {
        return {
            form: {
                emailUser: '',
                title: '',
                content: '',
                file_url: ''
            }
        }
    },
    methods: {
        handleAvatarScucess(val) {
            this.form.file_url = val.data.url
        },
        submit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = {}
                    Object.assign(param, this.form)
                    this.$api.apiCommunication('/Meeting/sendEmail', param, response => {
                        this.dialogVisible = false
                        if (response.status === 200) {
                            this.$notify({ title: '', message: '', type: 'success' })
                        } else {
                            this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                        }
                    })
                } else {
                    this.$notify({ title: '', message: '', type: 'error' })
                }
            })
        },
    },
    components: {
        fengFileUpload
    }
}
</script>



