<template>
    <div class="user-wrap">
        <h1 class="wrap-title mar-bottom15"> <el-button type="primary" @click="addEditorData(0, 'add')" size="mini" class="bread-addbtn"></el-button></h1>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item pdd-10">
                <el-table :data="moduleData.list" style="width: 100%">
                    <el-table-column prop="user_id" label="#" width="70"></el-table-column>
                    <el-table-column prop="name" label="" width="100"></el-table-column>
                    <el-table-column prop="mobile" label="" width="140"></el-table-column>
                    <el-table-column prop="email" label="" width="180" show-overflow-tooltip></el-table-column>
                    <el-table-column label="" width="130">
                        <template slot-scope="scope">
                            {{ scope.row.auth | authStatus }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="description" label="" width="130" show-overflow-tooltip></el-table-column>
                    <el-table-column label="" min-width="180">
                        <template slot-scope="scope">
                            <el-button type="primary" @click="addEditorData(scope, 'editor')" size="small"></el-button>
                            <el-button type="danger" @click="deleteData(scope)" size="small"></el-button>
						</template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible">
            <el-form ref="form" :model="form" label-width="80px" class="content-form">
                <el-form-item label="">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.auth" placeholder="" filterable>
                        <el-option label="" :value="1"></el-option>
                        <el-option label="" :value="2"></el-option>
                        <el-option label="" :value="3"></el-option>
                        <el-option label="" :value="4"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.mobile"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.email"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.password"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.description"></el-input>
                </el-form-item>
                <el-form-item label=" ">
                    <el-button type="primary" size="small" @click="submitEditor('form')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data () {
        return {
            marked: 'add',
            dialogVisible: false,
            form: {
                name: '',
                auth: 1,
                mobile: '',
                email: '',
                password: '',
                description: ''
            },
            moduleData: {
                list: []
            }
        }
    },
    created() {
        this.getModuleData()
    },
    computed: {
        dialogTitle() { return this.marked === 'add' ? '' : '' }
    },
    methods: {
        getModuleData() {
            this.$api.apiCommunication('/User/getUserList', {}, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list ? response.data.list : []
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        addEditorData(val, type) {
            if(type === 'editor') {
                let param = val.row
                delete param.create_time
                delete param.sex
                delete param.status
                delete param.update_time
                this.form = param
            } else {
                this.form = {
                    name: '',
                    auth: 1,
                    mobile: '',
                    email: '',
                    password: '',
                    description: ''
                }
            }
            this.marked = type
            this.dialogVisible = true
        },
        submitEditor(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = {},
                        apiname = this.marked === 'add' ? '/User/createUser' : '/User/updateUser'
                    Object.assign(param, this.form)
                    this.$api.apiCommunication(apiname, param, response => {
                        this.dialogVisible = false
                        if (response.status === 200) {
                            this.getModuleData()
                        } else {
                            this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                        }
                    })
                } else {
                    this.$notify({ title: '', message: '', type: 'error' })
                }
            })
        },
        deleteData(val) {
            this.$api.apiCommunication('/User/deleteUser', { user_id: val.row.user_id }, response => {
                if (response.status === 200) {
                    this.getModuleData()
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        }
    }
}
</script>



