<template>
    <div id="login">
        <div class="login-header">
            <div class="header-logo">
                <!-- <h1>
                    <a href="">
                        logo
                        <img src="../../static/img/logo.png" alt="">
                    </a>
                </h1> -->
                <div class="header-title"></div>
            </div>
        </div>
        <div class="login-content">
            <div class="content-wrap">
                <div class="form-wrap">
                    <h2>USER LOGIN</h2>
                    <h1></h1>
                    <el-form :model="form" :rules="rules" ref="form">
                        <el-form-item prop="email">
                            <el-input placeholder="" v-model="form.email" class="mar-top20" size="large">
                                <template slot="prepend">
                                    <i class="iconfont icon-account fs-1s2em"></i>
                                </template>
    						</el-input>
    					</el-form-item>
    					<el-form-item prop="password">
    						<el-input type="password" placeholder="" v-model="form.password" class="mar-top20" size="large" @keyup.enter.native="onLogin('form')">
    						    <template slot="prepend">
                                    <i class="iconfont icon-Password fs-1s2em"></i>
                                </template>
    						</el-input>
    					</el-form-item>
    					<el-button type="primary" @click="onLogin('form')" class="content-login"></el-button>
    				</el-form>
                </div>
                <div class="login-footer">
                    <p></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// 
import '@/static/style/login/index.scss'

export default {
    data() {
        return {
            form: {
                email: '',
                password: ''
            },
            rules: {
                email: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 20, message: ' 5  20 ', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        onLogin(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 
                    this.$store.dispatch('onLogin', this.form)
                } else {
                    this.$notify({ title: '', message: '', type: 'warning' })
                }
            })
        }
    }
}
</script>



