/**
 * 
 */

exports.install = (Vue, options) => {
    // 
    Vue.filter('authStatus', value => {
        let _val = ''
        switch (Number(value)) {
            case 0:
                _val = ''
                break
            case 1:
                _val = ''
                break
            case 2:
                _val = ''
                break
            case 3:
                _val = ''
                break
            default:
                _val = ''
        }
        return _val
    })
    // 
    Vue.filter('setsex', value => {
        let _val = ''
        switch (value) {
            case '0':
                _val = ''
                break;
            case 1:
                _val = ''
                break;
            case 2:
                _val = ''
                break;
            default:
                _val = ''
        }
        return _val
    })
    // 
    Vue.filter('topicType', value => {
        let _val = ''
        switch (Number(value)) {
            case 1:
                _val = ' 1'
                break
            case 2:
                _val = ' 2'
                break
            case 3:
                _val = ''
                break
            default:
                _val = ''
        }
        return _val
    })
    // 
    Vue.filter('toFixed', value => {
        if(value === -Infinity) {
            return 0
        }
        return value.toFixed(2)
    })
    // 
    Vue.filter('toString', value => value.toString())

    // 
    Vue.prototype.com = (() => {
        /**
         * Url
         * @param {String} url 
         * @param {String} target 
         * @return {Boolen}
         */
        const openUrl = (url, target) => {
            if(target) {
                window.location.href = url
            } else {
                window.open(url)
            }
            return true
        }
        /**
         * 
         * @param {String} format 
         * @return {String}
         */
        const getNowTime = format => {
            let d = new Date(),
                year = d.getFullYear(),
                month = d.getMonth() + 1,
                date = d.getDate(),
                hours = d.getHours(),
                minutes = d.getMinutes(),
                seconds = d.getSeconds()
            if(format !== 'yyyy-mm-dd hh-mm-ss'){
                return `${year}-${month}-${date}`
            }
            return `${year}-${month}-${date}-${hours}-${minutes}-${seconds}`
        }
        /**
         * 
         * @param  {Object} row   
         * @param  {Number} index 
         * @return {String}
         */
        const tableRowDisabled = (row, index) => {
            if(row.status === 0) {
                return 'row-disabled'
            }
        }
        return {
            openUrl: openUrl,
            getNowTime: getNowTime,
            tableRowDisabled: tableRowDisabled
        }
     })()
}



