<template>
    <el-table :data="moduleData.list" style="width: 100%">
        <el-table-column prop="id" label="#" width="70"></el-table-column>
        <el-table-column prop="name" label="" width="180"></el-table-column>
        <el-table-column prop="phone" label="" width="180"></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
    </el-table>
</template>

<script>
//  =>  => 
export default {
    props: ['meetingId'],
    data() {
        return {
            moduleData: {
                list: []
            }
        }
    },
    methods: {
        getModuleData() {
            this.$api.apiCommunication('/User/getUserConfereesList', { conference_id: this.meetingId }, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list ? response.data.list : []
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
    }
}
</script>



