/**
 * api
 */
import Vue from 'vue'
import axios from 'axios'
import { basicConfig } from '@/config/'
import querystring from 'querystring'
import store from '@/store'
import { MessageBox, Notification } from 'element-ui'

export default {
    /**
      * api
      */
    apiCommunication(apiname, param, callback, type) {
        let params = param
        if(apiname !== '/User/login') {
            Object.assign(params, { token: store.getters.getToken })
        }
        axios.post(basicConfig.API_HOST + apiname, querystring.stringify(params))
            .then(response => {
                callback(response.data)
            })
    }
}

// 
axios.interceptors.response.use(response => {
    if(response.status === 200) {
        if(response.data.status === 0) {
            // token
            // store.dispatch('switchLoadingStatus', false)
            MessageBox.alert('', '', {
                confirmButtonText: '',
                type: 'error',
                callback: action => {
                    store.dispatch('outLogin')
                }
            })
        } else {
            return response
        }
    }else {
        MessageBox.alert('[warning]', '', { confirmButtonText: '', type: 'error' })
    }
}, error => {
    // store.dispatch('switchLoadingStatus', false)
    MessageBox.alert(`[error]: ${error}`, '', { confirmButtonText: '', type: 'error' })
    return Promise.reject(error)
})



