<template>
    <div>
        <h1 class="wrap-title mar-bottom15"> <el-button type="primary" @click="addEditorData(0, 'add')" size="mini" class="bread-addbtn"></el-button></h1>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item pdd-10">
                <el-table :data="moduleData.list" style="width: 100%">
                    <el-table-column prop="room_id" label="#" width="70"></el-table-column>
                    <el-table-column prop="title" label="" width="150" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="location" label="" width="140"></el-table-column>
                    <el-table-column prop="seats_num" label="" width="100" ></el-table-column>
                    <el-table-column prop="manager_id" label="" width="100"></el-table-column>
                    <el-table-column prop="img_map" label="" width="130">
                        <template slot-scope="scope">
                            <el-popover width="350" trigger="click">
                                <img width="100%" :src="scope.row.img_map" alt="">
                                 <el-button size="mini" type="info" slot="reference"></el-button>
                            </el-popover>
                        </template>
                    </el-table-column>
                    <el-table-column label="" width="90">
                        <template slot-scope="scope">
                            {{ scope.row.status | meetingroomStatus }}
                        </template>
                    </el-table-column>
                    <el-table-column label="" min-width="250">
                        <template slot-scope="scope">
                            <el-button type="primary" @click="addEditorData(scope, 'editor')" size="small"></el-button>
                            <el-button type="danger" @click="deleteData(scope)" size="small"></el-button>
                            <el-button type="success" size="small" @click="seatSetting(scope)"></el-button>
						</template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible">
            <el-form ref="form" :model="form" label-width="95px" class="content-form">
                <el-form-item label="">
                    <el-input v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.location"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.seats_num"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.manager_id"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <feng-image-upload :imageUrl="form.img_map" @increment="handleAvatarScucess"></feng-image-upload>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.status" placeholder="" filterable>
                        <el-option label="" :value="1"></el-option>
                        <el-option label="" :value="-1"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=" ">
                    <el-button type="primary" size="small" @click="submitEditor('form')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import fengImageUpload from '@/components/fengModule/imageUpload.vue'

export default {
    data () {
        return {
            marked: 'add',
            dialogVisible: false,
            form: {
                title: '',
                location: '',
                seats_num: '',
                manager_id: '',
                img_map: '',
                status: 1
            },
            moduleData: {
                list: []
            }
        }
    },
    created() {
        this.getModuleData()
    },
    computed: {
        dialogTitle() { return this.marked === 'add' ? '' : '' }
    },
    methods: {
        getModuleData() {
            this.$api.apiCommunication('/Rooms/getRoomsList', {}, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        addEditorData(val, type) {
            if(type === 'editor') {
                let param = val.row
                delete param.create_time
                delete param.room_code
                delete param.update_time
                this.form = param
            } else {
                this.form = {
                    title: '',
                    location: '',
                    seats_num: '',
                    manager_id: '',
                    img_map: '',
                    status: 1
                }
            }
            this.marked = type
            this.dialogVisible = true
        },
        submitEditor(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = {},
                        apiname = this.marked === 'add' ? '/Rooms/createRooms' : '/Rooms/updateRooms'
                    Object.assign(param, this.form)
                    this.$api.apiCommunication(apiname, param, response => {
                        this.dialogVisible = false
                        if (response.status === 200) {
                            this.getModuleData()
                        } else {
                            this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                        }
                    })
                } else {
                    this.$notify({ title: '', message: '', type: 'error' })
                }
            })
        },
        deleteData(val) {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$api.apiCommunication('/Rooms/deleteRooms', { room_id: val.row.room_id }, response => {
                    if (response.status === 200) {
                        this.getModuleData()
                    } else {
                        this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                    }
                })
            }).catch(() => {
                // 
            })
        },
        handleAvatarScucess(row) {
            // 
            this.form.img_map = row
        },
        seatSetting(val) {
            this.$router.push({ name: 'seatSetting', params: { id: val.row.room_id } })
        }
    },
    filters: {
        meetingroomStatus(val) {
            return val === 1 ? '' : ''
        }
    },
    components: {
        fengImageUpload
    }
}
</script>



