<template>
    <div>
        <el-table :data="moduleData.list" style="width: 100%">
            <el-table-column prop="conferee_id" label="#" width="70"></el-table-column>
            <el-table-column prop="name" label="" width="100"></el-table-column>
            <el-table-column prop="title" label="" width="90"></el-table-column>
            <el-table-column prop="mobile" label="" width="120"></el-table-column>
            <el-table-column prop="email" label="" width="180"></el-table-column>
            <el-table-column prop="seat_code" label="" width="100"></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    {{ scope.row.can_sync_screen | canSyncScreenStatus }}
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
//  => 
export default {
    props: ['meetingId'],
    data () {
        return {
            moduleData: {
                list: []
            }
        }
    },
    created() {
        this.getModuleData()
    },
    methods: {
        getModuleData() {
            this.$api.apiCommunication('/User/getUserConfereesList', { conference_id: this.meetingId }, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list ? response.data.list : []
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        }
    },
    filters: {
        canSyncScreenStatus(val) {
            return val == 1 ? '' : ''
        }
    }
}
</script>



