<template>
    <div>
        <h1 class="wrap-title mar-bottom15"></h1>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item pdd-10">
                <!-- <div class=""></div> -->
                <el-table :data="moduleData.list" style="width: 100%">
                    <el-table-column prop="conference_id" label="#" width="50"></el-table-column>
                    <el-table-column prop="title" label="" width="250" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="status_name" label="" width="100"></el-table-column>
                    <el-table-column label="" min-width="180">
                        <template slot-scope="scope">
                            <el-button type="primary" @click="checkMeeting(scope)" size="small" v-if="scope.row.status != '3'"></el-button>
                            <el-button type="danger" @click="startEndMeeting(scope, 'start')" size="small" v-if="scope.row.status == '1'"></el-button>
                            <el-button type="danger" @click="startEndMeeting(scope, 'end')" size="small" v-if="scope.row.status == '2'"></el-button>
                            <el-button type="danger" @click="produceNotes(scope)" size="small" v-if="scope.row.status == '3'"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
    </div>
</template>

<script>
// 
export default {
    data () {
        return {
            nowMeeting: '',
            activeName: 'info',
            moduleData: {
                list: []
            }
        }
    },
    created() {
        this.getMeetingList()
    },
    methods: {
        getMeetingList() {
            this.$api.apiCommunication('/Meeting/getMeetingList', {}, response => {
                if (response.status === 200) {
                    if(response.data !== []) {
                        this.moduleData.list = response.data.list
                    } else {
                        this.moduleData.list = []
                    }
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        checkMeeting(val) {
            this.$router.push({ name: 'editorMeeting', params: { id: val.row.conference_id } })
        },
        startEndMeeting(val, type) {
            let _tips = type === 'start' ? '' : '',
                param = {
                    conference_id: val.row.conference_id,
                    status: type === 'start' ? 2 : 3
                }
            this.$confirm(_tips, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$api.apiCommunication('/Meeting/endMeeting', param, response => {
                    if (response.status === 200) {
                        this.$notify({ title: '', message: '', type: 'success' })
                        this.getMeetingList()
                    } else {
                        this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                    }
                })
            }).catch(() => {
                // 
            })

        },
        produceNotes(val) {

        }
    }
}
</script>



