<template>
    <div>
        <el-upload :action="actionUrl" :show-file-list="false" :data="requestData" :on-success="handleAvatarScucess" :on-error="handleAvatarError" :before-upload="beforeAvatarUpload">
            <el-button size="mini" type="warning"></el-button>
            <!-- <div slot="tip" class="el-upload__tip" v-if="imageUrl">{{ imageUrl }}</div> -->
        </el-upload>
        <template v-if="imageUrl">
            <img style="width: 150px;" :src="imageUrl" alt="">
            <!--
            <el-popover ref="popoverimg" placement="right" width="350" trigger="click">
                <img width="100%" :src="imageUrl" alt="">
            </el-popover>
            <el-button size="mini" type="primary" v-popover:popoverimg></el-button> -->
        </template>
    </div>
</template>

<script>
/**
 * [/]
 * @imageUrl url
 */
import '@/static/style/fengModule/imageUpload.scss'
import { basicConfig } from '@/config/'

export default {
    props: {
        imageUrl: {
            type: String
        },
        params: {
            type: Object,
            default: function () {
                return {}
            }
        }
    },
    data() {
        return {
            actionUrl: `${basicConfig.API_HOST}/Upload/imgUpload`,
            dialogVisible: false
        }
    },
    computed: {
        requestData() {
            return Object.assign({ token: this.$store.getters.getToken }, this.params)
        }
    },
    methods: {
        beforeAvatarUpload(file) {
            // 
            const isJPG = (file.type === 'image/jpeg' || file.type === 'image/png')
            const isLt2M = file.size / 1024 / 1024 < 2
            if (!isJPG) {
              this.$message.error(' JPG/PNG !')
            }
            if (!isLt2M) {
              this.$message.error(' 2MB!')
            }
            return isJPG && isLt2M
        },
        handleAvatarScucess(response, file) {
            // 
            if(response.status === 404) {
                this.$alert(response.data, '', { confirmButtonText: '', type: 'error' })
                return false
            }
            this.$emit('increment',response.data)
        },
        handleAvatarError(err, file) {
            // 
            this.$notify({ title: '', message: '', type: 'error' })
        },
        showDialog() {
            // 
            this.dialogVisible = true
        }
    }
}
</script>



