<template>
    <el-table :data="moduleData.list" style="width: 100%">
        <el-table-column prop="sign_in_id" label="#" width="70"></el-table-column>
        <el-table-column prop="name" label="" width="180"></el-table-column>
        <el-table-column prop="create_time" label="" width="180"></el-table-column>
        <el-table-column prop="seat_code" label=""></el-table-column>
    </el-table>
</template>

<script>
//  => 
export default {
    props: ['meetingId'],
    data() {
        return {
            moduleData: {
                list: []
            }
        }
    },
    created() {
        this.getModuleData()
    },
    methods: {
        getModuleData() {
            this.$api.apiCommunication('/Meeting/signUserList', { conference_id: this.meetingId }, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data ? response.data : []
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
    }
}
</script>



