<template>
    <div>
        <el-table :data="moduleData.list" style="width: 100%">
            <el-table-column prop="topic_id" label="#" width="70"></el-table-column>
            <el-table-column prop="title" label="" width="180"></el-table-column>
            <el-table-column label="" width="140">
                <template slot-scope="scope">
                    {{ scope.row.topic_type | topicType }}
                </template>
            </el-table-column>
            <el-table-column prop="email" label=""></el-table-column>
        </el-table>
        <el-button type="primary" size="small" class="mar-top15" @click="addEditorData(0, 'add')"></el-button>
        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible">
            <el-form ref="form" :model="form" label-width="95px" class="content-form">
                <el-form-item label="">
                    <el-input v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.topic_type" placeholder="" filterable>
                        <el-option label=" 1" :value="1"></el-option>
                        <el-option label=" 2" :value="2"></el-option>
                        <el-option label="" :value="3"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" v-if="form.topic_type == '3'">
                    <el-input v-model="form.topic_items"></el-input>
                </el-form-item>
                <el-form-item label=" ">
                    <el-button type="primary" size="small" @click="submitEditor('form')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
//  =>  => 
export default {
    props: ['meetingId'],
    data () {
        return {
            marked: 'add',
            dialogVisible: false,
            form: {
                title: '',
                topic_type: '',
                topic_items: ''
            },
            moduleData: {
                list: []
            }
        }
    },
    created() {
        this.getModuleData()
    },
    computed: {
        dialogTitle() { return this.marked === 'add' ? '' : '' }
    },
    methods: {
        getModuleData() {
            // conference_id
            this.$api.apiCommunication('/Meeting/getVoteList', { conference_id: this.meetingId }, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list ? response.data.list : []
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        addEditorData(val, type) {
            if(type === 'editor') {
                // this.form.config_id = val.row.config_id
            } else {
                this.form = {
                    title: '',
                    topic_type: '',
                    topic_items: ''
                }
            }
            this.marked = type
            this.dialogVisible = true
        },
        submitEditor(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = {
                            conference_id: this.meetingId,
                            status: 1
                        },
                        apiname = this.marked === 'add' ? '/Meeting/createVote' : '/Meeting/updateAgenda'
                    Object.assign(param, this.form)
                    if(param.topic_type != 3){
                        delete param.topic_items
                    }
                    this.$api.apiCommunication(apiname, param, response => {
                        this.dialogVisible = false
                        if (response.status === 200) {
                            this.$notify({ title: '', message: '', type: 'success' })
                            this.getModuleData()
                        } else {
                            this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                        }
                    })
                } else {
                    this.$notify({ title: '', message: '', type: 'error' })
                }
            })
        },
        deleteData(val) {
            this.$api.apiCommunication('/Rooms/deleteRoomsConfig', { config_id: val.row.config_id }, response => {
                if (response.status === 200) {
                    this.getModuleData()
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        }
    }
}
</script>



