            </el-submenu>
            <el-submenu index="4">
                <template slot="title">
                    <i class="iconfont icon-huiyi"></i>
                    <span></span>
                </template>
                <el-menu-item index="/addMeeting"></el-menu-item>
                <el-menu-item index="/nowMeeting"></el-menu-item>
                <el-menu-item index="/endMeeting"></el-menu-item>
            </el-submenu>
            <!-- <el-menu-item index="/addMeeting">
                <i class="el-icon-setting"></i>
                <span slot="title"></span>
            </el-menu-item>
            <el-menu-item index="/nowMeeting">
                <i class="el-icon-setting"></i>
                <span slot="title"></span>
            </el-menu-item>
            <el-menu-item index="/endMeeting">
                <i class="el-icon-setting"></i>
                <span slot="title"></span>
            </el-menu-item> -->
        </el-menu>
    </div>
</template>

<script>
import '@/static/style/nav/index.scss'

export default {
    data () {
        return {
            isCollapse: true,
            defaultElmenu: this.$route.path
        }
    },
    computed: {
        navCollapse() { return this.$store.getters.navCollapse }
    }
}
</script>



