<template>
    <div>
        <el-table :data="moduleData.list" style="width: 100%">
            <el-table-column prop="conferee_id" label="#" width="70"></el-table-column>
            <el-table-column prop="name" label="" width="100"></el-table-column>
            <el-table-column prop="title" label="" width="90"></el-table-column>
            <el-table-column prop="mobile" label="" width="120"></el-table-column>
            <el-table-column prop="email" label="" width="180"></el-table-column>
            <el-table-column prop="seat_code" label="" width="100"></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    {{ scope.row.can_sync_screen | canSyncScreenStatus }}
                </template>
            </el-table-column>
        </el-table>
        <el-button type="primary" size="small" class="mar-top15" @click="addEditorData(0, 'add')"></el-button>
        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible">
            <el-form ref="form" :model="form" label-width="95px" class="content-form">
                <el-form-item label="">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.title" placeholder="" filterable>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.password"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.mobile"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.email"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.seat_id" placeholder="" filterable>
                        <el-option label="" :value="0"></el-option>
                        <el-option :label="item.seat_code" :value="item.seat_id" :key="item.seat_id" v-for="item in seatList"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=" ">
                    <el-checkbox v-model="form.can_sync_screen"></el-checkbox>
                </el-form-item>
                <el-form-item label=" ">
                    <el-button type="primary" size="small" @click="submit('form')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
//  =>  => 
export default {
    props: ['meetingId'],
    data () {
        return {
            checked: true,
            marked: 'add',
            dialogVisible: false,
            form: {
                name: '',
                title: '',
                password: '',
                mobile: '',
                email: '',
                seat_id: '',
                can_sync_screen: false
            },
            seatList: [],
            moduleData: {
                list: []
            }
        }
    },
    created() {
        this.getModuleData()
        this.getSeatsList()
    },
    computed: {
        userId() { return this.$store.getters.getUserId },
        dialogTitle() { return this.marked === 'add' ? '' : '' }
    },
    methods: {
        getModuleData() {
            this.$api.apiCommunication('/User/getUserConfereesList', { conference_id: this.meetingId }, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list ? response.data.list : []
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        getSeatsList() {
            // conference_id
            this.$api.apiCommunication('/Rooms/getSeatsList', { conference_id: this.meetingId }, response => {
                if (response.status === 200) {
                    console.log(response)
                    this.seatList = response.data.list ? response.data.list : []
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        addEditorData(val, type) {
            if(type === 'editor') {
                // this.form.config_id = val.row.config_id
            } else {
                this.form = {
                    name: '',
                    title: '',
                    password: '',
                    mobile: '',
                    email: '',
                    seat_id: '',
                    can_sync_screen: false
                }
            }
            this.marked = type
            this.dialogVisible = true
        },
        submit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = {
                            conference_id: this.meetingId,
                            status: 1
                        },
                        apiname = this.marked === 'add' ? '/User/createUserConferees' : '/Meeting/updateAgenda'
                    Object.assign(param, this.form)
                    param.can_sync_screen = param.can_sync_screen == true ? 1 : 0
                    this.$api.apiCommunication(apiname, param, response => {
                        this.dialogVisible = false
                        if (response.status === 200) {
                            this.$notify({ title: '', message: '', type: 'success' })
                            this.getModuleData()
                        } else {
                            this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                        }
                    })
                } else {
                    this.$notify({ title: '', message: '', type: 'error' })
                }
            })
        },
        deleteData(val) {
            this.$api.apiCommunication('/Rooms/deleteRoomsConfig', { config_id: val.row.config_id }, response => {
                if (response.status === 200) {
                    this.getModuleData()
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        }
    },
    filters: {
        canSyncScreenStatus(val) {
            return val == 1 ? '' : ''
        }
    }
}
</script>



